<template>
    <div class="chooser-component">
        <ul class="chooser-list">
          <li v-for="(item,index) in selections" :title="item.label" :class="{active:index==nowIndex}" @click="chosenSelection(index)">{{item.label}}</li>
        </ul>
    </div>
</template>

<script>
export default {
  name: 'chooser',
  props:{
      selections:{
          type:Array,
          default:[{
              label:'test',
              value:0
          }]
      }
  },
  data () {
    return {
      nowIndex:0
    }
  },
  methods:{
      chosenSelection(index){
          this.nowIndex=index;
          this.$emit('onChange',this.selections[index]);
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.chooser-component {
  position: relative;
  display: inline-block;
}
.chooser-list li{
  display: inline-block;
  border: 1px solid #e3e3e3;
  height: 25px;
  line-height: 25px;
  padding: 0 8px;
  margin-right: 5px;
  border-radius: 3px;
  text-align: center;
  cursor: pointer;
}
.chooser-list li.active {
  border-color: #5CA6DB;
  background: #5CA6DB;
  color: #fff;
}

</style>



