import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/Index'
import Detail from '@/pages/Detail'
import Analysis from '@/pages/Analysis'
import Count from '@/pages/Count'
import Forecast from '@/pages/Forecast'
import Publish from '@/pages/Publish'
import OrderList from '@/pages/OrderList'


Vue.use(Router)

export default new Router({
    // mode: 'history',
    routes: [{
        path: '/',
        name: 'index',
        component: Index
    }, {
        path: '/detail',
        name: 'detail',
        component: Detail,
        redirect: '#/detail/count',
        children: [{
            path: 'count',
            name: 'count',
            component: Count
        }, {
            path: 'analysis',
            name: 'analysis',
            component: Analysis
        }, {
            path: 'forecast',
            name: 'forecast',
            component: Forecast
        }, {
            path: 'publish',
            name: 'publish',
            component: Publish
        }]
    }, {
        path: '/orderlist',
        name: 'orderlist',
        component: OrderList
    }]
})


