<template>
  <div>
    <mydialog :isShow="isShowCheckDialog" @click="checkStatus">
        <p class="check-p">请按确定按钮检查您的支付状态！</p>
        <div class="button check-btn" @click="checkStatus">
            确定
        </div>
    </mydialog>
    <mydialog :isShow="isShowSuccessDialog" @onclose="toOrderList">
        购买成功！！！
    </mydialog>

    <mydialog :isShow="isShowFailDialog" @onclose="toOrderList" >
         购买失败！！！
    </mydialog>
  </div>
</template>

<script>
import dialog from '@/components/Dialog'
import axios from 'axios'
export default {
  name:'checkorder',
  props:{
    isShowCheckDialog:{
        type:Boolean,
        default:false
    },
    orderId:{
        type:[String,Number]
    }
  },
  data () {
    return {
        isShowSuccessDialog:false,
        isShowFailDialog: false
    }
  },
  methods: {
    checkStatus(){
        axios.post('./static/db.json',{orderId:this.orderId}).then((res)=>{
            this.isShowSuccessDialog = true;
            this.$emit('onclose-check-dialog');
        }).catch((err)=>{
            this.isShowFailDialog = true;
            this.$emit('onclose-check-dialog');
        })
    },
    toOrderList(){
        this.$router.push({path: '/orderList'})
    }
  },
  components:{
    mydialog:dialog
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



