<template>
<div class="order-wrap">
    <h3>您的产品</h3>
    <div class="order-list-choose">
      <div class="order-list-option">
        选择产品：
        <v-select :selections="products" @onChange="productChange"></v-select>
      </div>

      <div class="order-list-option">
        开始日期：
       <v-date @onChange="getStartDate"></v-date>
      </div>

      <div class="order-list-option">
        结束日期：
        <v-date @onChange="getEndDate"></v-date>
      </div>

      <div class="order-list-option">
        关键词：
        <input type="text" v-model.lazy="query" class="order-query">
      </div>
    </div>
    <div class="order-list-table">
      <table>
        <tr>
          <th v-for="item in tableHeads" :class="{active:item.active}" @click="changeOrderType(item)">{{item.label}}</th>
        </tr>
        <tr v-for="item in tableData">
          <td v-for="head in tableHeads">{{item[head.key]}}</td>
        </tr>
      </table>
    </div>
</div>
</template>

<script>
import vSelect from '@/components/Selection'
import datepicker from '@/components/DatePicker'
import axios from 'axios'
import _ from 'lodash'
export default {
  name: 'orderlist',
  data () {
    return {
      products: [
        {
          label: '数据统计',
          value: 1
        },
        {
          label: '数据预测',
          value: 2
        },
        {
          label: '流量分析',
          value: 3
        },
        {
          label: '广告发布',
          value: 4
        }
      ],
      query:'',
      productId: 0,
      startDate: '',
      endDate: '',
      tableHeads: [
        {
          label: '订单号',
          key: 'orderId'
        },
        {
          label: '购买产品',
          key: 'product'
        },
        {
          label: '版本类型',
          key: 'version'
        },
        {
          label: '有效时间',
          key: 'period'
        },
        {
          label: '购买日期',
          key: 'date'
        },
        {
          label: '数量',
          key: 'buyNum'
        },
        {
          label: '总价',
          key: 'amount'
        }
      ],
      tableData: [],
      currentOrder: 'asc',
      
    }
  },
  watch:{
    query(){
      console.log('111');
      this.getList();
    }
  },
  components:{
    vSelect,
    vDate:datepicker
  },
  methods:{
    productChange(obj){
      this.productId = obj.value;
      this.getList();
    },
     getStartDate (date) {
      this.startDate = date
      this.getList();
    },
    getEndDate (date) {
      this.endDate = date;
      this.getList();
    },
    getList(){
      let reqParams={
        query: this.query,
        productId: this.productId,
        startDate: this.startDate,
        endDate: this.endDate
      }
      axios.post('./static/db.json', reqParams).then(res=>{
        this.tableData=res.data.appData.getOrderList.list;
        console.log(res.data.appData.getOrderList.list);
      }).catch(err=>{
        console.log(err)
      })
    },
    changeOrderType (headItem) {
      this.tableHeads.map((item) => {
        item.active = false
        return item
      })
      headItem.active = true
      if (this.currentOrder === 'asc') {
        this.currentOrder = 'desc'
      }
      else if (this.currentOrder === 'desc') {
        this.currentOrder = 'asc'
      }
      this.tableData = _.orderBy(this.tableData, headItem.key, this.currentOrder)
    }
  },
  mounted(){
    this.getList();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.order-wrap {
  width: 1200px;
  min-height: 800px;
  margin: 20px auto;
  overflow: hidden;
}
.order-wrap h3 {
  font-size: 20px;
  font-weight: bold;
  margin-bottom: 20px;
}
.order-query {
  height: 25px;
  line-height: 25px;
  border: 1px solid #e3e3e3;
  outline: none;
  text-indent: 10px;
}
.order-list-option {
  display: inline-block;
  padding-left: 15px;
}
.order-list-option:first-child {
  padding-left: 0;
}
.order-list-table {
  margin-top: 20px;
}
.order-list-table table {
  width: 100%;
  background: #fff;
}
.order-list-table td,
.order-list-table th {
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 10px 0;
}
.order-list-table th {
  background: #5CA6DB;
  color: #fff;
  border: 1px solid #5CA6DB;
  cursor: pointer;
}
.order-list-table th.active {
  background: #35495e;
}
</style>



