<template>
  <div class="login-form">
    <div class="g-form">
      <div class="g-form-line">
        <span class="g-form-label">用户名：</span>
        <div class="g-form-input">
          <input type="text" v-model="userName" placeholder="请输入用户名">
        </div>
        <span class="g-form-error">{{userErrors.errorText}}</span>
      </div>
      <div class="g-form-line">
        <span class="g-form-label">密码：</span>
        <div class="g-form-input">
          <input type="password" v-model="passWord" placeholder="请输入密码">
        </div>
        <span class="g-form-error">{{passwordErrors.errorText}}</span>
      </div>
      <div class="g-form-line">
        <div class="g-form-btn">
          <a class="button" @click="login">登录</a>
        </div>
      </div>
      <p class="g-form-total-msg">{{errorText}}</p>
    </div>
  </div>
</template>

<script>
import axios from 'axios' 
export default {
  name: 'log',
  data () {
    return {
      userName:'',
      passWord:'',
      errorText:'',
      userFlag:false,
      pwdFlag:false
    }
  },
  computed:{
    userErrors(){
        let errorText,status
        if(!/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{1,6}$/.test(this.userName)){
            status=false;
            errorText="用户名只能由6位字母和数字组成"
        }else{
            status=true;
            errorText="";
        }
        if(!this.userFlag){
            errorText="";
            this.userFlag=true;
        }
        return{
            errorText,
            status
        }
    },
    passwordErrors(){
       let errorText,status
        if(!/^\w{1,6}$/g.test(this.passWord)){
            status=false;
            errorText="密码不是1-6位"
        }else{
            status=true;
            errorText="";
        }
        if(!this.pwdFlag){
            errorText="";
            this.pwdFlag=true;
        }
        return{
            errorText,
            status
        } 
    }
  },
  methods:{
      login:function(){
          if(!this.userErrors.status || !this.passwordErrors.status){
              this.errorText="部分选项有错误，请重新检查"
          }else{
              this.errorText="";
              axios.get('./static/db.json').then(res =>{
                this.$emit('is-log',res.data.appData)
              }).catch(err =>{
                console.log(err)
              })
          }
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



