<template>
<div class="index-wrap">
    <div class="index-left">
      <div class="index-left-block">
        <h2>全部产品</h2>
        <template v-for="product in productList">
          <h3>{{product.title}}</h3>
          <ul>
            <li v-for="item in product.list">
              <a :href="item.url">{{item.name}}</a>
              <span v-if="item.hot" class="hot-tag">HOT</span>
            </li>
          </ul>
          <div v-if="!product.last" class="hr"></div>
        </template>
        
        
      </div>
      <div class="index-left-block lastest-news">
        <h2>最新消息</h2>
        <ul>
          <li v-for="item in newList">
            <a :href="item.url" class="new-item">{{item.title}}</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="index-right">
      <slidebanner :slides="slides" :time="time"></slidebanner>
      <div class="index-board-list">
        <div class="index-board-item" v-for="(item,index) in boardList" :class="[{'line-last':index % 2!=0},'index-board-'+item.id]">
          <div class="index-board-item-inner">
            <h2>{{ item.title }}</h2>
            <p>{{ item.description }}</p>
             <div class="index-board-button">
              <router-link class="button" :to="{path: 'detail/'+item.toKey}">立即购买</router-link>
            </div> 
          </div>
        </div>
      </div>
      
        
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import slidebanner from '@/components/SlideBanner'
export default {
  name: 'index',
  data () {
    return {
      productList:{
        pc:{
          title:'pc产品',
          list:[
            {
              name: '数据挖掘',
              url: 'detail/count'
            },
            {
              name: '后端产品',
              url: 'detail/forecast'
            },
            {
              name: '前端产品',
              url: 'detail/analysis',
              hot: true
            },
            {
              name: 'ui产品',
              url: 'detail/publish'
            }
          ]
        },
        app:{
          title:'app产品',
          last:true,
          list:[
            {
              name: '91助手',
              url: 'http://weixin.com'
            },
            {
              name: '产品助手',
              url: 'http://twitter.com',
              hot: true
            },
            {
              name: '智能地图',
              url: 'http://maps.com'
            },
            {
              name: '团队语音',
              url: 'http://phone.com'
            }
          ]
        }
      },
      newList:[],
      boardList: [
        {
          title: '数据挖掘',
          description: '数据挖掘是一款开放产品',
          id: 'car',
          toKey: 'count',
          saleout: false
        },
        {
          title: '前端产品',
          description: '前端产品帮助你的前端更好地找到定位',
          id: 'earth',
          toKey: 'analysis',
          saleout: false
        },
        {
          title: '后端产品',
          description: '后端产品快速迭代永远保持最全面的产品',
          id: 'loud',
          toKey: 'forecast',
          saleout: true
        },
        {
          title: 'ui产品',
          description: '最好用的ui切图软件',
          id: 'hill',
          toKey: 'publish',
          saleout: false
        }
      ],
      slides: [
        {
          src: require('../assets/slide/1.jpg'),
          title: 'css',
          href: 'detail/analysis'
        },
        {
          src: require('../assets/slide/2.jpg'),
          title: 'html',
          href: 'detail/count'
        },
        {
          src: require('../assets/slide/3.jpg'),
          title: 'js',
          href: 'detail/publish'
        },
        {
          src: require('../assets/slide/4.jpg'),
          title: 'vue',
          href: 'detail/forecast'
        }
      ],
      time:2000
    }
  },
  created:function(){
    axios.get('./static/db.json').then(response=>{
      // console.log(response.data.appData.newsList)
      this.newList=response.data.appData.newsList;
    }).catch(error=>{
      console.log(error)
    })
  },
  components:{
     slidebanner
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.index-wrap {
  width: 1200px;
  margin: 0 auto;
  overflow: hidden;
}
.index-left {
  float: left;
  width: 300px;
  text-align: left;
}
.index-right {
  float: left;
  width: 900px;
}
.index-left-block {
  margin: 15px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
}
.index-left-block .hr {
  margin-bottom: 20px;
}
.index-left-block h2 {
  background: #5CA6DB;
  color: #fff;
  padding: 10px 15px;
  margin-bottom: 20px;
}
.index-left-block h3 {
  padding: 0 15px 5px 15px;
  font-weight: bold;
  color: #222;
}
.index-left-block ul {
  padding: 10px 15px;
}
.index-left-block li {
  padding: 5px;
}
.index-board-list {
  overflow: hidden;
}
.index-board-item {
  float: left;
  width: 400px;
  background: #fff;
  box-shadow: 0 0 1px #ddd;
  padding: 20px;
  margin-right: 20px;
  margin-bottom: 20px;
}
.index-board-item-inner {
  min-height: 125px;
  padding-left: 120px;
}
.index-board-car .index-board-item-inner{
  background: url(../assets/images/1.png) no-repeat;
  background-size:100px 100px;
}
.index-board-loud .index-board-item-inner{
  background: url(../assets/images/2.png) no-repeat;
  background-size:100px 100px;
}
.index-board-earth .index-board-item-inner{
  background: url(../assets/images/3.png) no-repeat;
  background-size:100px 100px;
}
.index-board-hill .index-board-item-inner{
  background: url(../assets/images/4.png) no-repeat;
  background-size:100px 100px;
}
.index-board-item h2 {
  font-size: 18px;
  font-weight: bold;
  color: #000;
  margin-bottom: 15px;
}
.line-last {
  margin-right: 0;
}
.index-board-button {
  margin-top: 20px;
}
.lastest-news {
  min-height: 512px;
}
.hot-tag {
  background: red;
  color: #fff;
}
.new-item {
  display: inline-block;
  width: 230px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
</style>


