<template>
  <div>
    <div class="app-head">
        <div class="app-head-inner">
            <router-link :to="{path:'/vueec'}">
              <img src="../assets/logo.png">
            </router-link>
            <div class="head-nav">
                <ul class="nav-list">
                    <li> {{ username }}</li>
                    <li v-if="username!==''" class="nav-pile">|</li>
                    <li v-if="username!==''" @click="quit">退出</li>
                    <li v-if="username==''" @click="logClick">登录</li>
                    <li class="nav-pile">|</li>
                    <li v-if="username==''" @click="regClick">注册</li>
                    <li v-if="username==''" class="nav-pile">|</li>
                    <li @click="aboutClick">关于</li>
                </ul>
            </div>  
      </div>
    </div>
    <div class="app-content">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <div class="app-foot">
        <p>© 2017 by longerick</p>
    </div>
    <mydialog :is-show="isShowAboutDialog" @onclose="closeDialog('isShowAboutDialog')">
      <p>这里是关于内容。<br>在直观概念上，业界将电商划分为狭义与广义，狭义电商（E-Commerce）是指实现整个贸易过程中各阶段贸易活动的电子化，而广义电商（E-Business）是指利用网络实现所有商务活动业务流程的电子化。前者集中于基于互联网的电子交易，强调企业利用互联网与外部发生交易与合作；而后者则把涵盖范围扩大了很多，指企业使用各种电子工具从事商务活动。</p>
    </mydialog>
    <mydialog :is-show="isShowLogDialog" @onclose="closeDialog('isShowLogDialog')">
      <mylog @is-log="onSuccessLog"></mylog>
    </mydialog>
    <mydialog :is-show="isShowRegDialog" @onclose="closeDialog('isShowRegDialog')">
      <myreg @is-reg="onSuccessReg"></myreg>
      
    </mydialog>
    <mydialog :isShow="isShowRegDiogMsg" @onclose="ShowRegDiogMsg">
          欢迎你，注册成功！！！
    </mydialog>
  </div>
</template>

<script>
import dialog from '@/components/Dialog'
import log from '@/components/Log'
import reg from '@/components/Reg'
export default {
  name: 'layout',
  data () {
    return {
      isShowAboutDialog:false,
      isShowLogDialog:false,
      isShowRegDialog:false,
      username:'',
      isShowRegDiogMsg:false
    }
  },
  components:{
    mydialog:dialog,
    mylog:log,
    myreg:reg
  },
  methods:{
    aboutClick:function(){
      this.isShowAboutDialog=true;
    },
    logClick:function(){
      this.isShowLogDialog=true;
    },
    regClick:function(){
      this.isShowRegDialog=true;
    },
    closeDialog:function(arr){
      this[arr]=false;
    },
    onSuccessLog:function(data){
      this.closeDialog ('isShowLogDialog');
      this.username = data.login.username;
    },
    onSuccessReg:function(){
      // this.closeDialog ('isShowRegDialog');
      this.isShowRegDiogMsg=true;
      this.isShowRegDialog=false;
    },
    quit:function(){
      this.username="";
    },
    ShowRegDiogMsg:function(){
      // this.isShowRegDiogMsg=true;
      // this.closeDialog ('isShowRegDialog');
      // this.closeDialog ('isShowRegDiogMsg');
      this.isShowRegDiogMsg=false;
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
  display: block;
}
body {
  line-height: 1;
}
ol, ul {
  list-style: none;
}
blockquote, q {
  quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
body {
  background: #f0f2f5;
  font-family: "Helvetica Neue",Helvetica,Arial,"Hiragino Sans GB","Hiragino Sans GB W3","Microsoft YaHei UI","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
  font-size: 14px;
  color: #444;
}
.app-head {
  background: #999;
  color: #000000;
  height: 90px;
  line-height: 90px;
  width: 100%;
}
.app-head-inner {
  width: 1200px;
  margin: 0 auto;
}
.head-logo {
  float: left;
}
.app-head-inner img {
  width: 70px;
  margin-top: 10px;
  height: 70px;
  border-radius: 50%;
  margin-left:14px;
}
.head-nav {
  float: right;
}
.head-nav ul {
  overflow: hidden;
}
.head-nav li {
  cursor: pointer;
  float: left;
}
.nav-pile {
  padding: 0 10px;
}
.app-foot {
  text-align: center;
  height: 80px;
  width: 100%;
  line-height: 80px;
  background: #e3e4e8;
  clear: both;
  margin-top: 30px;
}
.container {
  width: 1200px;
  margin: 0 auto;
}
.hr {
  height: 1px;
  width: 100%;
  background: #ddd;
}
.button {
  background: #5CA6DB;
  color: #fff;
  display: inline-block;
  padding: 10px 20px;
  cursor: pointer;
  border:1px solid #5CA6DB;
}
.button:hover {
  background: #ffffff;
  color:#5CA6DB;
  border:1px solid #5CA6DB;
}
.g-form {

}
.g-form-line {
  padding: 15px 0;
}
.g-form-label {
  width: 100px;
  font-size: 16px;
  display: inline-block;
}
.g-form-input {
  display: inline-block;
}
.g-form-input input {
  height: 30px;
  width: 200px;
  line-height: 30px;
  vertical-align: middle;
  padding: 0 10px;
  border: 1px solid #ccc;
}
.g-form-btn {
  padding-left: 100px;
}
.g-form-error {
  color: red;
  padding-left: 15px;
}

.g-form-total-msg{
  color:red;
}

/* CheckOrder部分样式 */
.check-btn{
  margin-left:45%;
  margin-top:20px;
}

.check-p{
  text-align:center;
}
</style>




