<template>
  <div class="sales-board">
      <div class="sales-board-intro">
        <h2>前端产品</h2>
        <p>是指在获得网站访问量基本数据的情况下对有关数据进行统计、分析，从中发现用户访问网站的规律，并将这些规律与网络营销策略等相结合，从而发现目前网络营销活动中可能存在的问题，并为进一步修正或重新制定网络营销策略提供依据。当然这样的定义是站在网络营销管理的角度来考虑的</p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  购买数量：
              </div>
              <div class="sales-board-line-right">
                  <v-count :max="buyNumberRange.max" :min="buyNumberRange.min" @onChange="getParamChange('buyNumber',$event)"></v-count>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品类型：
              </div>
              <div class="sales-board-line-right">
                  <v-select :selections="buyTypes" @onChange="getParamChange('buyType',$event)"></v-select>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  有效时间：
              </div>
              <div class="sales-board-line-right">
                  <v-choose :selections="periodList" @onChange="getParamChange('buyPeriod',$event)"></v-choose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  产品版本：
              </div>
              <div class="sales-board-line-right">
                <v-mulchoose :selections="versionList" @onChange="getParamChange('buyVersions',$event)"></v-mulchoose>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  总价：
              </div>
              <div class="sales-board-line-right">
                 {{price}}元
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    立即购买
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2>产品说明</h2>
        <p>网站访问统计分析报告的基础数据源于网站流量统计信息，但其价值远高于原始数据资料。专业的网站访问统计分析报告对网络营销的价值，正如专业的财务分析报告对企业经营策略的价值。</p>

        <h3>用户行为指标</h3>
        <ul>
          <li>用户行为指标主要反映用户是如何来到网站的、在网站上停留了多长时间、访问了哪些页面等，主要的统计指标包括：</li>
          <li>用户在网站的停留时间；</li>
          <li>用户来源网站（也叫“引导网站”）；</li>
          <li>用户所使用的搜索引擎及其关键词；</li>
          <li>在不同时段的用户访问量情况等。</li>
        </ul>

        <h3>浏览网站方式</h3>
        <ul>
          <li>用户上网设备类型</li>
          <li>用户浏览器的名称和版本</li>
          <li>访问者电脑分辨率显示模式</li>
          <li>用户所使用的操作系统名称和版本</li>
          <li>用户所在地理区域分布状况等</li>
        </ul>
      </div>
      <mydialog :isShow="isShowPayDialog" @onclose="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th>购买数量</th>
            <th>产品类型</th>
            <th>有效时间</th>
            <th>产品版本</th>
            <th>总价</th>
          </tr>
          <tr>
            <td>{{ buyNumber }}</td>
            <td>{{ buyType.label }}</td>
            <td>{{ buyPeriod.label }}</td>
            <td>
              <span v-for="item in buyVersions">{{ item.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title">请选择银行</h3>
        <bank-choose  @onchange="onChangeBanks"></bank-choose>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          确认购买
        </div>
      </mydialog>
      <mydialog :isShow="isShowErrDialog" @onclose="hideErrDialog">
          支付失败！
      </mydialog>
      <my-check :is-show-check-dialog="isShowCheckOrder" @onclose-check-dialog="hideCheckOrder" :order-id="orderId">
      </my-check>
  </div>
</template>

<script>
import vChoose from '@/components/Chooser'
import vSelect from '@/components/Selection'
import vMulchoose from '@/components/MulChooser'
import vCount from '@/components/Counter'
import dialog from '@/components/Dialog'
import bankChoose from '@/components/BankChooser'
import myCheck from '@/components/CheckOrder'
import _ from 'lodash'
import axios from 'axios'
export default {
  name: 'analysis',
  data () {
    return {
      buyTypes: [
        {
          label: '入门版',
          value: 1
        },
        {
          label: '中级版',
          value: 2
        },
        {
          label: '高级版',
          value: 3
        }
      ],
      periodList:[
        {
          label: '半年',
          value: 1
        },
        {
          label: '一年',
          value: 2
        },
        {
          label: '三年',
          value: 3
        }
      ],
      versionList: [
        {
          label: '客户版',
          value: 1
        },
        {
          label: '代理商版',
          value: 2
        },
        {
          label: '专家版',
          value: 3
        }
      ],
      buyNumberRange:{
        max:20,
        min:1
      },
      isShowPayDialog: false,
      isShowErrDialog:false,
      isShowCheckOrder: false,
      //组件上传的购买参数
      buyNumber:0,
      buyType:{},
      buyVersions:[],
      buyPeriod:{},
      price:0,
      bankId:null,
      orderId:null
      
    }
  },
  components:{
      vChoose,
      vSelect,
      vMulchoose,
      vCount,
      mydialog:dialog,
      bankChoose,
      myCheck
  },
  methods:{
    // 获取子组件传递过来的值
    getParamChange(attr,value){
      this[attr] = value;
      this.getPrice();
    },
    // 计算总价格
    getPrice(){
      // 获取购买版本数组里面的value
      let buyVersionArr=_.map(this.buyVersions,(el)=>{
        return el.value
      })

      // 所有请求参数
      let reqParam={
        buyNumber: this.buyNumber,
        buyType: this.buyType.value,
        buyPeriod: this.buyPeriod.value,
        version: buyVersionArr.join(',')
      }

      // console.log(reqParam);

      axios.post('./static/db.json',{reqParam}).then(res=>{
        // console.log(res)
        this.price=res.data.appData.getPrice.amount;
      }).catch(err=>{
        console.log(err)
      })

    },
    //关闭打开支付弹窗
    showPayDialog () {
      this.isShowPayDialog = true
    },
    hidePayDialog () {
      this.isShowPayDialog = false
    },
    confirmBuy(){
      let buyVersionArr=_.map(this.buyVersions,(el)=>{
        return el.value
      })
      let reqParam={
        buyNumber: this.buyNumber,
        buyType: this.buyType.value,
        buyPeriod: this.buyPeriod.value,
        version: buyVersionArr.join(','),
        bankId:this.bankId
      }

      axios.post('./static/db.json',{reqParam}).then(res=>{
        console.log(res.data.appData.createOrder.orderId);
        this.orderId=res.data.appData.createOrder.orderId;
        this.isShowCheckOrder = true
        this.isShowPayDialog = false
      }).catch(err=>{
        console.log(err);
        this.isShowPayDialog = false
        this.isShowErrDialog = true
      })
    },
    //银行选择
    onChangeBanks(bankObj){
      this.bankId = bankObj.id
    },
    hideErrDialog () {
      this.isShowErrDialog = false
    },
    hideCheckOrder () {
      this.isShowCheckOrder = false
    },
  },
  mounted:function(){
    this.buyNumber=1;
    this.buyType=this.buyTypes[0];
    this.buyVersions=[this.versionList[0]];
    this.buyPeriod=this.periodList[0];
    this.getPrice();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.buy-dialog-title {
  font-size: 16px;
  font-weight: bold;
}
.buy-dialog-btn {
  margin-top: 20px;
}
.buy-dialog-table {
  width: 100%;
  margin-bottom: 20px;
}
.buy-dialog-table td,
.buy-dialog-table th{
  border: 1px solid #e3e3e3;
  text-align: center;
  padding: 5px 0;
}
.buy-dialog-table th {
  background: #5CA6DB;
  color: #fff;
  border: 1px solid #5CA6DB;
}
</style>



